	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked: James' Web Development website","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/actionscript-2-to-actionscript-3-communication-try-flashinterface/index.md absPath of file >>> MarkdownRemark","html":"<p>As you probably know, Actionscript 2 and Actionscript 3 projects aren't completely compatable - they can't be, what with all the changes under the hood.  Migrating your own coding techniques is hard enough, but migrating projects can be so much easier and nigh-on-impossible.  So, how could you make a mish-mash of Actionscript 2 and Actionscript 3 movies play nice?</p>\n<p>Well, there's a few solutions, and several classes that claim to do the job, but the best I've found is <a href=\"http://www.flashextensions.com/products/flashinterface/examples/01_flashinterface_avms.html\">FlashInterface</a>.  It might take a bit of figuring out at first, but the Events based model means that I find it quite easy to use.</p>\n<h2>Links</h2>\n<ul>\n<li><a href=\"http://www.flashextensions.com/products/flashinterface/examples/01_flashinterface_avms.html\">FlashInterface demonstration</a>.</li>\n</ul>","frontmatter":{"title":"Actionscript 2 to Actionscript 3 communication? try FlashInterface...","date":"August 20, 2008","tags":["external-links"],"url":null,"image":null,"description":null}}},"pathContext":{"prev":{"frontmatter":{"path":"/blog/creating-outlook-calendar-events-through-http-links/","tags":["html","http-link","icalendar","outlook"],"title":"Creating Outlook Calendar events through hyperlinks","date":"2008-08-07T22:45:02.000Z","description":null,"image":null}},"next":{"frontmatter":{"path":"/blog/just-how-easy-can-you-make-an-air-application-installation/","tags":["air","auto-updater"],"title":"Just how easy can you make an AIR application installation?","date":"2008-08-23T21:00:54.000Z","description":null,"image":null}}}}

